import {
  Button,
  FilledInput,
  makeStyles,
  MenuItem,
  Modal,
  Select,
  Typography,
} from '@material-ui/core';
import React, { useState } from 'react';
import { useMutation, useQueryClient } from 'react-query';
import validator from 'validator';
import createUserAddress from '../services/user/createUserAddress';

const useStyles = makeStyles((theme) => ({
  container: {
    width: '40%',
    margin: 'auto',
    marginTop: '20vh',
    textAlign: 'center',
    [theme.breakpoints.down('xs')]: {
      width: '95%',
    },
  },
  fields: {
    padding: '10px',
    backgroundColor: 'white',
  },
}));

const AddNewAddress = ({ open, handleClose }) => {
  const classes = useStyles();
  const [addressInfo, setAddressInfo] = useState({
    f_name: '',
    address: '',
    nearest_landmark: '',
    country: 'Nepal',
    phone: '',
  });
  const [error, setError] = useState(null);
  const mutation = useMutation((newAddress) => createUserAddress(newAddress));
  const queryClient = useQueryClient();

  const handleAddressSubmit = (e) => {
    e.preventDefault();
    if (validator.isEmpty(addressInfo.f_name, { ignore_whitespace: true })) {
      setError('please enter valid full name');
      return;
    } else if (!validator.isMobilePhone(addressInfo.phone, ['ne-NP'])) {
      setError('please enter valid mobile number');
      return;
    } else if (
      validator.isEmpty(addressInfo.address, { ignore_whitespace: true })
    ) {
      setError('please enter valid address');
      return;
    }
    mutation.mutate(addressInfo, {
      onSuccess: () => {
        queryClient.invalidateQueries('userAddresses');
      },
    });
  };

  if (mutation.isSuccess) {
    handleClose();
  }

  if (mutation.isError) {
    setError(mutation.error.message);
  }

  return (
    <Modal open={open} onClose={handleClose} className={classes.container}>
      <form
        className={classes.fields}
        onSubmit={handleAddressSubmit}
        onChange={() => setError('')}
      >
        <Typography variant='h5' gutterBottom>
          Address Information
        </Typography>
        {error && (
          <Typography variant='subtitle1' color='error' gutterBottom>
            {error}
          </Typography>
        )}
        <FilledInput
          type='text'
          margin='dense'
          name='fullname'
          fullWidth
          value={addressInfo.f_name}
          onChange={(e) =>
            setAddressInfo({ ...addressInfo, f_name: e.target.value })
          }
          placeholder='Enter your full name'
          style={{ marginBottom: '10px' }}
        />
        <FilledInput
          type='text'
          margin='dense'
          name='address'
          value={addressInfo.address}
          placeholder='Your address eg. New Road, Mahadevsthan'
          onChange={(e) =>
            setAddressInfo({ ...addressInfo, address: e.target.value })
          }
          fullWidth
          style={{ marginBottom: '10px' }}
        />
        <FilledInput
          type='text'
          margin='dense'
          name='nearest_landmark'
          fullWidth
          placeholder='Nearest Landmark(optional) Near NMB Bank'
          onChange={(e) =>
            setAddressInfo({ ...addressInfo, nearest_landmark: e.target.value })
          }
          style={{ marginBottom: '10px' }}
        />

        <FilledInput
          type='text'
          margin='dense'
          name='country'
          fullWidth
          placeholder='Country'
          value={addressInfo.country}
          onChange={(e) =>
            setAddressInfo({ ...addressInfo, country: e.target.value })
          }
          style={{ marginBottom: '10px' }}
        />

        <FilledInput
          type='text'
          margin='dense'
          name='phone'
          fullWidth
          value={addressInfo.phone}
          onChange={(e) =>
            setAddressInfo({ ...addressInfo, phone: e.target.value })
          }
          placeholder='Phone'
          style={{ marginBottom: '10px' }}
        />
        <Button variant='outlined' color='primary' type='submit'>
          Save
        </Button>
      </form>
    </Modal>
  );
};

export default AddNewAddress;
