import React from 'react';
import {
  makeStyles,
  Grid,
  List,
  Typography,
  ListItem,
  Link,
} from '@material-ui/core';
import { Link as RouterLink } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  footer: {
    backgroundColor: '#fafafa',
    padding: '3rem',
    [theme.breakpoints.down('xs')]: {
      padding: '1rem 1rem',
    },
  },
  footer_title: {
    textDecoration: 'underline',
    textUnderlinePosition: 'under',
    fontSize: '1rem',
  },
  footerLink: {
    color: 'black',
    '&:hover': {
      textDecoration: 'none',
      color: theme.palette.primary.main,
    },
  },

  subscribe: {
    display: 'flex',
    alignItems: 'center',
  },
}));

const Footer = () => {
  const classes = useStyles();

  return (
    <div className={classes.footer}>
      <Grid container alignItems='center'>
        <Grid item md={4} sm={6} xs={12}>
          <List>
            <ListItem>
              <Typography variant='h5' className={classes.footer_title}>
                KBS
              </Typography>
            </ListItem>
            <ListItem>
              <Link
                component={RouterLink}
                to='/about'
                className={classes.footerLink}
              >
                <Typography>About Us</Typography>
              </Link>
            </ListItem>
            <ListItem>
              <Link
                component={RouterLink}
                to='/contact'
                className={classes.footerLink}
              >
                <Typography>Contact Us</Typography>
              </Link>
            </ListItem>
            <ListItem>
              <Link
                component={RouterLink}
                to='/delivery'
                className={classes.footerLink}
              >
                <Typography>Delivery Charges</Typography>
              </Link>
            </ListItem>
          </List>
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <List>
            <ListItem>
              <Typography variant='h5' className={classes.footer_title}>
                POLICY
              </Typography>
            </ListItem>
            <ListItem>
              <Link
                component={RouterLink}
                to='/'
                className={classes.footerLink}
              >
                <Typography>Terms of Usage</Typography>
              </Link>
            </ListItem>
            <ListItem>
              <Link
                component={RouterLink}
                to='/'
                className={classes.footerLink}
              >
                <Typography>Privacy Policy</Typography>
              </Link>
            </ListItem>
            <ListItem>
              <Link
                component={RouterLink}
                to='/'
                className={classes.footerLink}
              >
                <Typography>Return Policy</Typography>
              </Link>
            </ListItem>
          </List>
        </Grid>
        <Grid item md={4} sm={6} xs={12}>
          <List>
            <ListItem>
              <Typography variant='h5' className={classes.footer_title}>
                SOCIAL MEDIA
              </Typography>
            </ListItem>
            <ListItem>
              <a
                href='https://www.facebook.com/kadariyakosmetics'
                className={classes.footerLink}
                target='blank'
              >
                <i
                  className='fab fa-facebook-square'
                  style={{ fontSize: '2.5rem' }}
                ></i>
              </a>
            </ListItem>
            <ListItem>
              <a
                href='https://www.instagram.com/kadariya_kosmetics/'
                className={classes.footerLink}
                target='blank'
              >
                <i
                  className='fab fa-instagram'
                  style={{ fontSize: '2.5rem' }}
                ></i>
              </a>
            </ListItem>
          </List>
        </Grid>
      </Grid>
    </div>
  );
};

export default Footer;
