import React from 'react';
import { Box, Card, CardActionArea, CardMedia } from '@material-ui/core';
import { useQuery } from 'react-query';
import getBrands from '../services/products/getBrands';
import Loading from '../components/loading';
import Slider from 'react-slick';

const BrandImages = () => {
  const { isLoading: isBrandLoading, data: brands } = useQuery(
    'brands',
    getBrands
  );

  var settings = {
    dots: false,
    infinite: true,
    autoplay: true,
    centerMode: false,
    speed: 1000,
    autoplaySpeed: 3000,
    slidesToShow: 5,
    slidesToScroll: 2,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
        },
      },
      {
        breakpoint: 900,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  if (isBrandLoading) {
    return <Loading />;
  }

  return (
    <div style={{ margin: '2rem 2rem' }}>
      <Box
        display='flex'
        alignItems='center'
        justifyContent='space-evenly'
        flexWrap='wrap'
        style={{ marginTop: '1rem', padding: '0 2rem' }}
      >
        <Box style={{ width: '100%' }}>
          <Slider {...settings}>
            {brands.map((brand) => (
              <Box key={brand.id} marginY='1rem'>
                <img
                  src={`${process.env.REACT_APP_UPLOAD_URL}${brand.image}`}
                  alt=''
                  width='200px'
                />
              </Box>
            ))}
          </Slider>
        </Box>
      </Box>
    </div>
  );
};

export default BrandImages;
