import React from 'react';
import { Container, Box, Typography } from '@material-ui/core';
import ProductCard from '../components/productCard';

const AdvertisementProducts = (props) => {
  const { products } = props.location.state;

  console.log(products);

  return (
    <div style={{ minHeight: '75vh' }}>
      <Box marginY='3rem'>
        <Container maxWidth='lg'>
          <Typography
            variant='h4'
            align='center'
            style={{ marginBottom: '2rem' }}
          >
            Grab a deal on some of our fantastic products
          </Typography>
          <Box display='flex' flexWrap='wrap' justifyContent='space-between'>
            {products.map((product) => (
              <ProductCard
                key={product.id}
                name={product.product_name}
                subtitle={product.description}
                image={`${process.env.REACT_APP_UPLOAD_URL}${product.images[0].image}`}
                price={product.price}
                id={product.id}
                bestSeller={product.best_seller}
                brandName={product.brand && product.brand.brand_name}
              />
            ))}
          </Box>
        </Container>
      </Box>
    </div>
  );
};

export default AdvertisementProducts;
