import React, { useState } from 'react';
import {
  Typography,
  Button,
  Box,
  TableContainer,
  TableHead,
  TableRow,
  TableBody,
  Table,
  TableCell,
  makeStyles,
} from '@material-ui/core';
import AddNewAddress from './addNewAddress';
import { useQuery } from 'react-query';
import getUserAddresses from '../services/user/getUserAddresses';
import Loading from './loading';

const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: 650,
  },
  tableScroll: {
    width: '100%',
    [theme.breakpoints.down('sm')]: {
      width: 350,
    },
  },
}));

const AddressInfo = () => {
  const classes = useStyles();
  const [showAddAddressBox, setShowAddAddressBox] = useState(false);

  const handleAddAddressClose = () => {
    setShowAddAddressBox(false);
  };

  const { isLoading, data } = useQuery('userAddresses', () =>
    getUserAddresses()
  );

  if (isLoading) {
    return <Loading />;
  }

  const showAddresses = (addresses) => {
    return addresses.map((address) => (
      <TableRow key={address.id}>
        <TableCell align='right'>{address.fullname}</TableCell>
        <TableCell align='right'>{address.address}</TableCell>
        <TableCell align='right'>Rs. {address.location}</TableCell>
        <TableCell align='right'>{address.nearest_landmark}</TableCell>
      </TableRow>
    ));
  };

  return (
    <div>
      <Box
        display='flex'
        justifyContent='space-between'
        style={{ marginBottom: '2rem', padding: '0 1rem' }}
      >
        <Typography variant='h6' style={{ marginRight: '10px' }}>
          Address Information
        </Typography>
        <Button
          variant='outlined'
          size='small'
          onClick={() => setShowAddAddressBox(true)}
        >
          + Add
        </Button>
      </Box>
      <AddNewAddress
        open={showAddAddressBox}
        handleClose={handleAddAddressClose}
      />
      <TableContainer className={classes.tableScroll}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell align='right'>Fullname</TableCell>
              <TableCell align='right'>Address</TableCell>
              <TableCell align='right'>Location</TableCell>
              <TableCell align='right'>Nearest Landmark</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>{showAddresses(data)}</TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default AddressInfo;
