import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Alert } from '@material-ui/lab';
import {
  makeStyles,
  Card,
  List,
  ListItem,
  ListItemAvatar,
  Avatar,
} from '@material-ui/core';
import { ListItemText } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    top: 20,
    width: 300,
    zIndex: 2000,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    maxHeight: '75vh',
    overflowY: 'scroll',
    [theme.breakpoints.down('xs')]: {
      maxHeight: '80vh',
    },
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
}));

const SearchResult = ({
  result,
  showAlert,
  setShowAlert,
  isSearchOpen,
  setIsSearchOpen,
}) => {
  const classes = useStyles();
  const history = useHistory();

  if (result.length === 0) {
    return (
      <>
        {showAlert && (
          <Alert
            severity='info'
            style={{ position: 'absolute', top: 0, zIndex: 10 }}
            onClick={() => setShowAlert(false)}
            variant='filled'
          >
            No product Found ! Search brand or name
          </Alert>
        )}
      </>
    );
  }

  const handleItemClick = (id) => {
    setIsSearchOpen(false);
    history.push(`/product/${id}`);
  };

  return (
    <Card
      className={classes.paper}
      style={{ display: isSearchOpen ? 'block' : 'none' }}
    >
      <List>
        {result.map((item) => (
          <ListItem
            key={item.product_name}
            onClick={() => handleItemClick(item.id)}
            style={{ cursor: 'pointer' }}
          >
            <ListItemAvatar>
              <Avatar
                className={classes.large}
                src={`${process.env.REACT_APP_UPLOAD_URL}${item.images[0].image}`}
              />

              <ListItemText
                primary={item.product_name}
                secondary={item.brand.brand_name}
              />
            </ListItemAvatar>
          </ListItem>
        ))}
      </List>
    </Card>
  );
};

export default SearchResult;
