import { Typography, makeStyles, Box, Button } from '@material-ui/core';
import React, { useState } from 'react';
import { useQuery } from 'react-query';
import { Link as RouterLink } from 'react-router-dom';
import Loading from '../components/loading';
import ProductCard from '../components/productCard';
import getProductByCategory from '../services/products/getProductByCategory';

const useStyles = makeStyles((theme) => ({
  container: {
    padding: '1rem 4rem',
    [theme.breakpoints.down('sm')]: {
      padding: '1rem',
    },
  },
  product_container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  quick_links: {
    padding: '1rem',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
}));

const ProductByCategory = ({ match }) => {
  const classes = useStyles();
  const [lowtohigh, setlowtohigh] = useState('');
  const [order, setOrder] = useState('');

  const [text, setText] = useState('LOW TO HIGH');

  const { isLoading, data } = useQuery(
    ['productByCategory', match.params.id, { lowtohigh }],
    (args) => {
      return getProductByCategory(
        match.params.id,
        args.queryKey[2].lowtohigh,
        0
      );
    }
  );

  const handleLowToHigh = () => {
    if (text === 'LOW TO HIGH') {
      setlowtohigh(1);
      setText('HIGH TO LOW');
    } else {
      setlowtohigh('');
      setText('LOW TO HIGH');
    }
  };

  const handleSorting = () => {
    if (order === '' || 'ascending') {
      setOrder('ascending');
    }
    if (order !== 'descending') {
      setOrder('descending');
    }
  };

  if (isLoading) {
    return <Loading />;
  }

  if (data.length === 0) {
    return (
      <Typography
        variant='h5'
        style={{ height: '70vh', padding: '2rem' }}
        align='center'
        color='primary'
      >
        No products to show currently
      </Typography>
    );
  }

  const renderProducts = (data) => {
    console.log('dasdas', data);
    if (order === 'ascending') {
      data
        .sort(function (a, b) {
          if (a.product_name < b.product_name) {
            return -1;
          } else if (a.product_name > b.product_name) {
            return 1;
          } else {
            return 0;
          }
        })
        .map((product) => (
          <ProductCard
            name={product.product_name}
            subtitle={product.brand && product.brand.brand_name}
            price={product.price}
            image={`${process.env.REACT_APP_UPLOAD_URL}${product.images[0].image}`}
            brandName={product.brand && product.brand.brand_name}
            bestSeller={product.best_seller}
            id={product.id}
            key={product.id}
            discount={product.discount_amount}
          />
        ));
    }

    if (order === 'descending') {
      data
        .sort(function (a, b) {
          if (a.product_name < b.product_name) {
            return 1;
          } else if (a.product_name > b.product_name) {
            return -1;
          } else {
            return 0;
          }
        })
        .map((product) => (
          <ProductCard
            name={product.product_name}
            subtitle={product.brand && product.brand.brand_name}
            price={product.price}
            image={`${process.env.REACT_APP_UPLOAD_URL}${product.images[0].image}`}
            brandName={product.brand && product.brand.brand_name}
            bestSeller={product.best_seller}
            id={product.id}
            key={product.id}
            discount={product.discount_amount}
          />
        ));
    }

    return data.map((product) => (
      <ProductCard
        name={product.product_name}
        subtitle={product.brand && product.brand.brand_name}
        price={product.price}
        image={`${process.env.REACT_APP_UPLOAD_URL}${product.images[0].image}`}
        brandName={product.brand && product.brand.brand_name}
        bestSeller={product.best_seller}
        id={product.id}
        discount={product.discount_amount}
        key={product.id}
      />
    ));
  };

  return (
    <div className={classes.container}>
      <div className={classes.quick_links}>
        <Box display='flex'>
          <Typography variant='h6'>
            <RouterLink to='/'>Home</RouterLink>
          </Typography>
          <Typography variant='h6' style={{ margin: '0 1rem' }}>
            <i className='fas fa-chevron-right'></i>
          </Typography>
          <Typography variant='h6'>
            {data[0].categorization[0].category_name}
          </Typography>
        </Box>
        <Box display='flex' flexWrap='wrap'>
          <Button
            onClick={handleLowToHigh}
            variant='outlined'
            color='primary'
            size='small'
            style={{ marginLeft: '10px' }}
          >
            {text}
          </Button>
          <Button
            variant='outlined'
            style={{ marginLeft: '10px' }}
            color='primary'
            size='small'
            onClick={handleSorting}
          >
            SORT (a-z | z-a)
          </Button>
        </Box>
      </div>
      <div className={classes.product_container}>{renderProducts(data)}</div>
    </div>
  );
};

export default ProductByCategory;
