import React, { useContext, useState } from 'react';
import {
  Button,
  FilledInput,
  FormControl,
  InputAdornment,
  InputLabel,
  makeStyles,
} from '@material-ui/core';
import axios from 'axios';
import { UserContext } from '../contexts/UserContext';
import { useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';

const useStyles = makeStyles((theme) => ({
  signup_container: {
    padding: '2rem 1rem',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
  },
  logo: {
    display: 'block',
    width: '120px',
    margin: 'auto',
    marginTop: '2rem',
  },
  signup_form: {
    width: '40%',
    margin: '1rem auto',
    [theme.breakpoints.down('sm')]: {
      width: '90%',
    },
  },
  signup_form_item: {
    marginBottom: '1rem',
  },
}));

const Signup = () => {
  const classes = useStyles();
  const [isDisabled, setIsDisabled] = useState(false);

  const [UserState, dispatch] = useContext(UserContext);
  const history = useHistory();

  const handleSignUp = async (e) => {
    e.preventDefault();
    setIsDisabled(true);

    let email = e.target.email.value;
    let password = e.target.password.value;
    let first_name = e.target.firstName.value;
    let last_name = e.target.lastName.value;
    let confirmPassword = e.target.confirmPassword.value;

    if (confirmPassword !== password) {
      toast.error("password didn't match");
      return;
    }

    try {
      const response = await axios.post(
        `${process.env.REACT_APP_API_URL}/accounts/signup/`,
        { email, password, first_name, last_name }
      );
      if (response.status === 201) {
        toast('Please check your email to verify and login', {
          className: 'toast-container',
        });
        history.push('/');
      }
    } catch (err) {
      console.log('err', err);
      dispatch({ type: 'LOGOUT' });
    }
  };

  return (
    <div className={classes.signup_container}>
      <img src='0.png' alt='logo' className={classes.logo} />
      <form className={classes.signup_form} onSubmit={handleSignUp}>
        <FormControl
          variant='filled'
          color='secondary'
          fullWidth={true}
          className={classes.signup_form_item}
        >
          <InputLabel htmlFor='filled-adornment-firstname'>
            Enter First Name
          </InputLabel>
          <FilledInput
            id='filled-adornment-firstname'
            type='text'
            margin='dense'
            name='firstName'
            style={{ backgroundColor: 'white' }}
          />
        </FormControl>
        <FormControl
          variant='filled'
          color='secondary'
          fullWidth={true}
          className={classes.signup_form_item}
        >
          <InputLabel htmlFor='filled-adornment-lastname'>
            Enter Last Name
          </InputLabel>
          <FilledInput
            id='filled-adornment-lastname'
            type='text'
            name='lastName'
            margin='dense'
            style={{ backgroundColor: 'white' }}
          />
        </FormControl>
        <FormControl
          variant='filled'
          color='secondary'
          fullWidth={true}
          className={classes.signup_form_item}
        >
          <InputLabel htmlFor='filled-adornment-email'>Enter Email</InputLabel>
          <FilledInput
            id='filled-adornment-email'
            type='email'
            margin='dense'
            name='email'
            style={{ backgroundColor: 'white' }}
          />
        </FormControl>
        <FormControl
          variant='filled'
          color='secondary'
          fullWidth={true}
          className={classes.signup_form_item}
        >
          <InputLabel htmlFor='filled-adornment-password'>
            Enter Password
          </InputLabel>
          <FilledInput
            id='filled-adornment-password'
            type='password'
            margin='dense'
            style={{ backgroundColor: 'white' }}
            name='password'
          />
        </FormControl>
        <FormControl
          variant='filled'
          color='secondary'
          fullWidth={true}
          className={classes.signup_form_item}
        >
          <InputLabel htmlFor='filled-adornment-password'>
            Confirm Password
          </InputLabel>
          <FilledInput
            id='filled-adornment-password'
            type='password'
            margin='dense'
            style={{ backgroundColor: 'white' }}
            name='confirmPassword'
          />
        </FormControl>
        <Button
          disabled={isDisabled}
          variant='contained'
          color='primary'
          fullWidth
          type='submit'
        >
          Create Account
        </Button>
      </form>
    </div>
  );
};

export default Signup;
