import React, { useState } from 'react';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Avatar,
  Box,
  Button,
  Container,
  FilledInput,
  makeStyles,
  Typography,
} from '@material-ui/core';
import Loading from './loading';
import { useMutation, useQuery, useQueryClient } from 'react-query';
import getProductReviews from '../services/products/getProductReviews';
import { Rating } from '@material-ui/lab';
import createReview from '../services/user/createReview';
import { useParams } from 'react-router-dom';
import { toast } from 'react-toastify';

const useStyles = makeStyles((theme) => ({
  container: {
    padding: '0 7rem',
    [theme.breakpoints.down('xs')]: {
      padding: '1rem',
    },
  },
}));

const CustomerReviews = ({ productId, reviewCount }) => {
  const classes = useStyles();
  const [isReviewAreaOpen, setIsReviewAreaOpen] = useState(false);
  const [review, setReview] = useState('');
  const [rating, setRating] = useState(4);
  const [subject, setSubject] = useState('');
  const params = useParams();
  const queryClient = useQueryClient();

  const mutation = useMutation((review) => createReview(review));

  const { isLoading, data } = useQuery(['product-reviews', productId], () =>
    getProductReviews(productId)
  );

  const handleReviewSubmit = () => {
    setReview('');
    setIsReviewAreaOpen(false);
    mutation.mutate(
      { id: params.id, subject, comment: review, rating },
      {
        onSuccess: () => {
          console.log('hello');
          queryClient.invalidateQueries('product-reviews');
        },
        onError: (err) => {
          toast.dark('Please login to leave us areview');
        },
      }
    );
  };

  const showReviews = (reviews) => {
    return reviews.map((review, i) => (
      <Box
        display='flex'
        alignItems='center'
        style={{ marginTop: '1.5rem' }}
        key={i}
      >
        <Box display='flex' flexDirection='column' alignItems='center'>
          <Avatar></Avatar>
          <Typography style={{ marginTop: '0.6rem' }}>
            {review.user.full_name}
          </Typography>
        </Box>
        <Box style={{ marginLeft: '2rem' }}>
          <Typography
            variant='subtitle2'
            style={{ fontSize: '1.2rem' }}
            gutterBottom
          >
            {review.subject}
          </Typography>
          <Rating precision={0.5} value={review.rate} readOnly={true} />
          <Typography gutterBottom variant='subtitle1'>
            {review.comment}
          </Typography>
        </Box>
      </Box>
    ));
  };

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div className={classes.container}>
      <Typography variant='h6' gutterBottom>
        Customer Reviews ({reviewCount})
      </Typography>
      <Accordion expanded={isReviewAreaOpen} elevation={0}>
        <AccordionSummary
          onClick={() => setIsReviewAreaOpen(!isReviewAreaOpen)}
          expandIcon={<i className='fas fa-caret-down'></i>}
          aria-controls='panel2bh-content'
          id='panel2bh-header'
        >
          <Typography color='primary'>Leave your product review</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Box style={{ width: '100%' }}>
            <FilledInput
              margin='dense'
              style={{
                width: '100%',
                fontSize: '0.8rem',
                backgroundColor: '#ffffff',
              }}
              multiline
              placeholder='Subject of the review'
              value={subject}
              onChange={(e) => setSubject(e.target.value)}
            />
          </Box>
        </AccordionDetails>
        <AccordionDetails>
          <Box style={{ width: '100%' }}>
            <FilledInput
              margin='dense'
              style={{
                width: '100%',
                fontSize: '0.8rem',
                backgroundColor: '#ffffff',
              }}
              multiline
              placeholder='Write your review here and dont forget to leave stars!'
              value={review}
              onChange={(e) => setReview(e.target.value)}
            />
          </Box>
        </AccordionDetails>
        <AccordionDetails>
          <Box
            display='flex'
            alignItems='center'
            justifyContent='space-between'
            style={{ flexBasis: '100%' }}
          >
            <Rating
              name='customized-empty'
              defaultValue={rating}
              value={rating}
              precision={0.5}
              onChange={(e) => setRating(e.target.value)}
            />
            <Button
              color='secondary'
              onClick={handleReviewSubmit}
              variant='outlined'
            >
              Submit Review
            </Button>
          </Box>
        </AccordionDetails>
      </Accordion>
      <Container>
        <div>{showReviews(data)}</div>
      </Container>
    </div>
  );
};

export default CustomerReviews;
