import React from 'react';
import { useQuery } from 'react-query';
import { Box, makeStyles, Typography } from '@material-ui/core';
import Loading from '../components/loading';
import SkinCareRoutineCard from '../components/skinCareRoutineCard';
import { Link as RouterLink } from 'react-router-dom';
import getSkinCareRoutines from '../services/user/getSkinCareRoutines';

const useStyles = makeStyles((theme) => ({
  container: {
    padding: '1rem 4rem',
    [theme.breakpoints.down('sm')]: {
      padding: '1rem',
    },
  },
  quick_links: {
    padding: '1rem',
    display: 'flex',
  },
  routines: {
    margin: '0 1rem',
  },
}));

const SkinCareRoutines = () => {
  const classes = useStyles();
  const { isLoading, data } = useQuery(
    'skincare-routines',
    getSkinCareRoutines
  );

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div className={classes.container}>
      <div className={classes.quick_links}>
        <Typography variant='h6'>
          <RouterLink to='/'>Home</RouterLink>
        </Typography>
        <Typography variant='h6' style={{ margin: '0 1rem' }}>
          <i className='fas fa-chevron-right'></i>
        </Typography>
        <Typography variant='h6'>Skin Care Routines</Typography>
      </div>
      <Box display='flex' flexWrap='wrap'>
        {data.map((item) => (
          <div className={classes.routines}>
            <SkinCareRoutineCard
              key={item.id}
              userName={item.first_name + ' ' + item.last_name}
              skinType={item.skin_type}
              morning={item.morning_skincare_routine}
              evening={item.evening_skincare_routine}
              image={item.image}
            />
          </div>
        ))}
      </Box>
    </div>
  );
};

export default SkinCareRoutines;
