import React from 'react';
import { useQuery } from 'react-query';
import { useHistory } from 'react-router-dom';
import {
  Container,
  Box,
  List,
  ListItem,
  ListItemText,
} from '@material-ui/core';
import getBrands from '../services/products/getBrands';
import Loading from '../components/loading';

const ProductBrand = () => {
  const { isLoading: isBrandLoading, data: brands } = useQuery(
    'brands',
    getBrands
  );
  const history = useHistory();

  if (isBrandLoading) {
    return <Loading />;
  }

  console.log(brands);

  return (
    <Box margin='3rem 0'>
      <Container>
        <List>
          {brands.map((brand) => {
            return (
              <div>
                <ListItem key={brand.id}>
                  <ListItemText
                    primary={brand.brand_name}
                    secondary={brand.brand_desc}
                    primaryTypographyProps={{
                      gutterBottom: true,
                      color: 'primary',
                      variant: 'h5',
                    }}
                    style={{ cursor: 'pointer' }}
                    onClick={() => history.push(`/brand/${brand.id}`)}
                  />
                </ListItem>
                {brand.image && (
                  <img
                    src={`${process.env.REACT_APP_UPLOAD_URL}${brand.image}`}
                    alt='brand_image'
                    height='300px'
                    width='300px'
                    style={{ objectFit: 'contain' }}
                  />
                )}
              </div>
            );
          })}
        </List>
      </Container>
    </Box>
  );
};

export default ProductBrand;
