import React, { useState } from 'react';
import {
  Card,
  CardActionArea,
  CardContent,
  CardMedia,
  Typography,
  makeStyles,
  CardActions,
  Button,
} from '@material-ui/core';
import TestimonialReadMore from './testimonialReadMore';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '300px',
    height: '550px',
    margin: '1rem',
  },
  media: {
    height: 250,
    objectFit: 'contain',
  },
}));

const TestomonialCard = ({ name, image, review, title }) => {
  const [showModal, setShowModal] = useState(false);

  const classes = useStyles();
  return (
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image={image}
          title='Contemplative Reptile'
        />
        <CardContent>
          <Typography variant='h6' color='primary'>
            {name}
          </Typography>
          <Typography gutterBottom color='primary'>
            {title}
          </Typography>
          <Typography gutterBottom variant='subtitle2'>
            {review.substring(0, 200)} ...
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button
          style={{ fontSize: '0.7rem' }}
          color='primary'
          onClick={() => setShowModal(true)}
        >
          Read More
        </Button>
      </CardActions>
      {showModal && (
        <TestimonialReadMore
          name={name}
          image={image}
          review={review}
          showModal={showModal}
          setShowModal={setShowModal}
        />
      )}
    </Card>
  );
};

export default TestomonialCard;
