import React from 'react';
import { makeStyles, Typography } from '@material-ui/core';
import AwesomeSlider from 'react-awesome-slider';
import withAutoplay from 'react-awesome-slider/dist/autoplay';
import AwsSliderStyles from 'react-awesome-slider/src/styles';
import 'react-awesome-slider/dist/custom-animations/fold-out-animation.css';
import { useQuery } from 'react-query';
import Loading from './loading';
import getBannerImages from '../services/products/getBannerImages';

const AutoplaySlider = withAutoplay(AwesomeSlider);

const useStyles = makeStyles((theme) => ({
  slider: {
    height: '65vh',
    [theme.breakpoints.down('xs')]: {
      height: '100%',
    },
  },
  sliderCaptionContainer_items: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    alignContent: 'center',
  },
}));

const ProductCaraousel = () => {
  const classes = useStyles();

  const { isLoading, data } = useQuery('images', getBannerImages);

  if (isLoading) {
    return <Loading />;
  }

  console.log(data);

  return (
    <div className={classes.sliderContainer}>
      <AutoplaySlider
        cssModule={AwsSliderStyles}
        play={true}
        interval={5000}
        cancelOnInteraction={false}
        animation='foldOutAnimation'
        bullets={false}
        className={classes.slider}
      >
        {data.map((img) => (
          <div
            key={img.id}
            data-src={`${process.env.REACT_APP_UPLOAD_URL}${img.image}`}
          ></div>
        ))}
      </AutoplaySlider>
    </div>
  );
};

export default ProductCaraousel;
