import api from '../api';

export default async function getProductByCategory(
  categoryId,
  lowtohigh,
  limit
) {
  let a = `/products?low_to_high=${lowtohigh}&product_category=${categoryId}`;
  if (limit !== 0) {
    a += '&limit=5';
  }
  const response = await api.get(a);
  return response.data;
}
