import React, { useState } from 'react';
import {
  Card,
  CardHeader,
  makeStyles,
  Avatar,
  Typography,
  IconButton,
  CardMedia,
  Button,
  CardContent,
  CardActions,
} from '@material-ui/core';
import { useHistory } from 'react-router';
import SkinCareReadMore from './skinCareReadMore';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '320px',
    [theme.breakpoints.down('xs')]: {
      width: 310,
    },
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
}));

const SkinCareRoutineCard = ({
  userName,
  skinType,
  morning,
  evening,
  image,
}) => {
  const classes = useStyles();
  const [showModal, setShowModal] = useState(false);
  const history = useHistory();

  return (
    <div style={{ marginTop: '1.5rem' }}>
      <Card className={classes.root}>
        <CardHeader
          avatar={
            <Avatar
              aria-label='recipe'
              className={classes.avatar}
              onClick={() => history.push('/')}
              src={`${process.env.REACT_APP_UPLOAD_URL}${image}`}
            >
              {userName[0]}
            </Avatar>
          }
          title={userName}
          subheader={'Skin Type: ' + skinType}
        />

        <CardContent style={{ minHeight: '200px' }}>
          <Typography variant='h6' gutterBottom>
            Morning
          </Typography>
          <Typography gutterBottom>{morning.substring(0, 50)}</Typography>
          <Typography variant='h6' gutterBottom>
            Evening
          </Typography>
          <Typography>{evening.substring(0, 50)}</Typography>
        </CardContent>
        <CardActions>
          <Button
            style={{ fontSize: '0.7rem' }}
            color='primary'
            onClick={() => setShowModal(true)}
          >
            Read More
          </Button>
        </CardActions>
        {showModal && (
          <SkinCareReadMore
            name={userName}
            morning={morning}
            evening={evening}
            showModal={showModal}
            setShowModal={setShowModal}
          />
        )}
      </Card>
    </div>
  );
};

export default SkinCareRoutineCard;
