import React, { useState, useContext, useEffect } from 'react';
import {
  Grid,
  Container,
  Paper,
  MenuItem,
  MenuList,
  makeStyles,
  Card,
  BottomNavigation,
  BottomNavigationAction,
} from '@material-ui/core';
import PersonalInfo from '../components/personalInfo';
import AddressInfo from '../components/addressInfo';
import MyOrders from '../components/myOrders';
import { useLocation } from 'react-router-dom';
import { UserContext } from '../contexts/UserContext';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  container: {
    margin: '2rem 0',
  },
  paper: {
    margin: '2rem',
    height: '60vh',
    [theme.breakpoints.down('sm')]: {
      display: 'none',
    },
  },
  navigationSm: {
    width: '100vw',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
}));

const Profile = () => {
  const classes = useStyles();
  const location = useLocation();

  const history = useHistory();

  const [UserState] = useContext(UserContext);

  useEffect(() => {
    if (UserState.user == null) {
      history.push('/signin');
    }
  }, [UserState.user, history]);

  let personalInfo, addressInfo, myOrders;
  if (location.state) {
    personalInfo = location.state.personalInfo;
    addressInfo = location.state.addressInfo;
    myOrders = location.state.myOrders;
  }

  const [profileComponent, setProfileComponent] = useState({
    personalInfo: myOrders ? false : true,
    addressInfo: addressInfo || false,
    myOrders: myOrders || false,
  });

  return (
    <Container>
      <Card className={classes.container}>
        <Grid container justify='space-evenly'>
          <Grid item md={4} sm={12}>
            <div className={classes.navigationSm}>
              <BottomNavigation showLabels style={{ backgroundColor: 'black' }}>
                <BottomNavigationAction
                  label='Profile'
                  icon={<i class='fas fa-address-card'></i>}
                  style={{ color: 'white' }}
                  onClick={() =>
                    setProfileComponent({
                      ...profileComponent,
                      personalInfo: true,
                      addressInfo: false,
                      myOrders: false,
                    })
                  }
                />
                <BottomNavigationAction
                  label='Address'
                  icon={<i className='fas fa-map-marker-alt'></i>}
                  style={{ color: 'white' }}
                  onClick={() =>
                    setProfileComponent({
                      ...profileComponent,
                      addressInfo: true,
                      myOrders: false,
                      personalInfo: false,
                    })
                  }
                />
                <BottomNavigationAction
                  label='Orders'
                  icon={<i className='fas fa-truck'></i>}
                  style={{ color: 'white' }}
                  onClick={() =>
                    setProfileComponent({
                      ...profileComponent,
                      myOrders: true,
                      personalInfo: false,
                      addressInfo: false,
                    })
                  }
                />
              </BottomNavigation>
            </div>
            <Paper className={classes.paper} elevation={0}>
              <MenuList>
                <MenuItem
                  divider
                  style={{ marginBottom: '1rem' }}
                  onClick={() =>
                    setProfileComponent({
                      ...profileComponent,
                      personalInfo: true,
                      addressInfo: false,
                      myOrders: false,
                    })
                  }
                >
                  Personal Information
                </MenuItem>
                <MenuItem
                  divider
                  style={{ marginBottom: '1rem' }}
                  onClick={() =>
                    setProfileComponent({
                      ...profileComponent,
                      addressInfo: true,
                      myOrders: false,
                      personalInfo: false,
                    })
                  }
                >
                  Address Information
                </MenuItem>
                <MenuItem
                  divider
                  style={{ marginBottom: '1rem' }}
                  onClick={() =>
                    setProfileComponent({
                      ...profileComponent,
                      myOrders: true,
                      personalInfo: false,
                      addressInfo: false,
                    })
                  }
                >
                  My Orders
                </MenuItem>
              </MenuList>
            </Paper>
          </Grid>
          <Grid item md={6} style={{ marginTop: '2rem' }}>
            {profileComponent.personalInfo && <PersonalInfo />}
            {profileComponent.addressInfo && <AddressInfo />}
            {profileComponent.myOrders && <MyOrders />}
          </Grid>
        </Grid>
      </Card>
    </Container>
  );
};

export default Profile;
