import { useEffect } from 'react';
import { useLocation, useHistory } from 'react-router-dom';
import { Typography } from '@material-ui/core';
import api from '../services/api';
import { toast } from 'react-toastify';

const ESewaPaymentSuccess = () => {
  const location = useLocation();
  const history = useHistory();

  console.log(location);

  useEffect(() => {
    async function verifyEsewa() {
      const res = await api.get(`/orders/esewa-verify${location.search}`);
      if (res.status === 200) {
        history.push('/');
        toast.info('Payment made successfully');
      } else {
        toast.error('Couldnt make the payment');
      }
    }
    verifyEsewa();
  }, [location, history]);

  return (
    <div style={{ minHeight: '70vh' }}>
      <Typography align='center' variant='h4' style={{ margin: '3rem 0' }}>
        Payment Success.
      </Typography>
      <Typography align='center'>Redirecting to home...</Typography>
    </div>
  );
};

export default ESewaPaymentSuccess;
