import React from 'react';
import {
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  TableCell,
  Table,
  TableBody,
  Button,
  Box,
  makeStyles,
} from '@material-ui/core';
import { useQuery } from 'react-query';
import { useHistory } from 'react-router';
import Loading from './loading';
import getUserOrder from '../services/user/getUserOrder';

const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: 650,
  },
  tableScroll: {
    width: '100%',
    [theme.breakpoints.down('sm')]: {
      width: 350,
    },
  },
}));

const MyOrders = () => {
  const { isLoading, data } = useQuery('my_orders', getUserOrder);
  const history = useHistory();

  const classes = useStyles();

  const handleEsewaPayment = (order) => {
    var path = `${process.env.REACT_APP_ESEWA_URL}`;
    var params = {
      amt: order.cost[0].order_subtotal,
      psc: 0,
      pdc: order.cost[0].delivery_cost,
      txAmt: 0,
      tAmt: order.cost[0].total,
      pid: `order_${order.order_number}`,
      scd: 'EPAYTEST',
      su: `${process.env.REACT_APP_URL}/esewa_payment_success`,
      fu: `${process.env.REACT_APP_URL}/esewa_payment_failed`,
    };

    function post(path, params) {
      var form = document.createElement('form');
      form.setAttribute('method', 'POST');
      form.setAttribute('action', path);

      for (var key in params) {
        var hiddenField = document.createElement('input');
        hiddenField.setAttribute('type', 'hidden');
        hiddenField.setAttribute('name', key);
        hiddenField.setAttribute('value', params[key]);
        form.appendChild(hiddenField);
      }

      document.body.appendChild(form);
      form.submit();
    }

    post(path, params);
  };

  const paymentOption = (order) => {
    if (
      order.payment_method === 'BankPayment' &&
      order.payment_status !== 'Paid'
    ) {
      return (
        <Box>
          <Button
            color='primary'
            variant='contained'
            size='small'
            endIcon={<i className='fas fa-arrow-right'></i>}
            onClick={() => history.push('/bank-details')}
          >
            Bank
          </Button>
        </Box>
      );
    }
    if (order.payment_status === 'Paid') {
      return <h3>Paid</h3>;
    }
    if (order.payment_method === 'COD' && order.payment_status === 'Unpaid') {
      return 'Cash On Delivery';
    }
    if (order.payment_method === 'Esewa' && order.payment_status === 'Unpaid') {
      return (
        <div onClick={() => handleEsewaPayment(order)}>
          <input
            style={{
              backgroundColor: 'green',
              border: 'none',
              padding: '8px',
              borderRadius: '5px',
              color: 'white',
              cursor: 'pointer',
            }}
            value='Pay With ESewa'
            type='submit'
          />
        </div>
      );
    }
  };

  const showOrders = (orders) => {
    return orders.map((order) => {
      return (
        <TableRow key={order.id}>
          <TableCell align='right'>{order.order_number}</TableCell>

          <TableCell align='right'>
            <Box>
              {order.products.map((item, i) => {
                return (
                  <img
                    key={i}
                    height='40px'
                    width='40px'
                    src={`${process.env.REACT_APP_UPLOAD_URL}${item.product.images[0].image}`}
                    alt='sd'
                    style={{ borderRadius: '50%', objectFit: 'contain' }}
                  />
                );
              })}
            </Box>
            {order.products.map((product) => {
              return (
                <span
                  key={product.id}
                >{`${product.product.product_name}( ${product.quantity} ), `}</span>
              );
            })}
          </TableCell>
          <TableCell align='right'>Rs. {order.cost[0].total}</TableCell>

          <TableCell align='right'>Rs. {order.cost[0].delivery_cost}</TableCell>

          <TableCell align='right'>
            Rs. {order.cost[0].order_subtotal}
          </TableCell>
          <TableCell align='right'>{paymentOption(order)}</TableCell>
          <TableCell align='right'>{order.order_status}</TableCell>
        </TableRow>
      );
    });
  };

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div>
      <Typography
        variant='h6'
        style={{ marginBottom: '2rem', padding: '0 1rem' }}
      >
        My Orders
      </Typography>
      <TableContainer className={classes.tableScroll}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell align='right'>Order Id</TableCell>
              <TableCell align='right'>Order Items</TableCell>
              <TableCell align='right'>Total Price</TableCell>
              <TableCell align='right'>Delivery Charge</TableCell>
              <TableCell align='right'>Subtotal</TableCell>
              <TableCell align='right'>Payment Method</TableCell>
              <TableCell align='right'>Order Status</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>{showOrders(data)}</TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default MyOrders;
