import {
  Avatar,
  Box,
  Button,
  ButtonGroup,
  Divider,
  Grid,
  IconButton,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  makeStyles,
  Typography,
} from '@material-ui/core';
import React, { useContext } from 'react';
import { useHistory } from 'react-router';
import { CartContext } from '../contexts/CartContext';
import { UserContext } from '../contexts/UserContext';

const useStyles = makeStyles((theme) => ({
  container: {
    minHeight: '50vh',
    padding: '3rem 5rem',
    [theme.breakpoints.down('xs')]: {
      padding: '3rem 10px',
    },
  },
  cartItems: {
    padding: '1rem',
    border: '3px solid #eeeeee',
  },
  avatar: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
  quantity: {
    [theme.breakpoints.down('sm')]: {
      display: 'none',
    },
  },
}));

const Cart = () => {
  const classes = useStyles();
  const {
    total,
    cartItems,
    itemCount,
    clearCart,
    removeProduct,
    increase,
    decrease,
  } = useContext(CartContext);
  const [UserState] = useContext(UserContext);

  const history = useHistory();

  const checkIfNotInStock = (productCount) => {
    if (productCount <= 0) {
      return true;
    } else {
      return false;
    }
  };

  const handleQtyIncrease = (item) => {
    if (item.quantity > item.product_stock - 2) return;
    increase(item);
  };

  const handleQtyDecrease = (item) => {
    if (item.quantity === 1) {
      return;
    }
    decrease(item);
  };

  const clearAllCartItems = () => {
    clearCart();
  };

  const handleCartCheckout = () => {
    if (!UserState.user) {
      history.push('/signin');
      return;
    }
    history.push('/checkout');
  };

  if (cartItems.length === 0) {
    return (
      <Typography
        variant='h4'
        align='center'
        style={{ marginTop: '2rem', minHeight: '60vh' }}
      >
        YOUR CART IS EMPTY
      </Typography>
    );
  }

  const showCartItems = () => {
    console.log('cartItems', cartItems);
    return cartItems.map((item) => (
      <ListItem key={item.id} className={classes.cartItems_item}>
        <Grid container>
          <Grid item sm={12} md={5}>
            <ListItemAvatar>
              <Avatar
                src={`${process.env.REACT_APP_UPLOAD_URL}${item.images[0].image}`}
                imgProps={{
                  alt: item.brand.brand_name,
                }}
                className={classes.avatar}
              />
            </ListItemAvatar>
            <ListItemText
              primary={item.product_name}
              secondary={`NPR. ${item.price - item.discount_amount}`}
              secondaryTypographyProps={{
                color: 'primary',
              }}
            />
          </Grid>
          <Grid item md={2}>
            <Typography
              variant='h6'
              color='textSecondary'
              className={classes.quantity}
            >
              Qty
            </Typography>
          </Grid>
          <Grid item md={5} xs={12}>
            <ButtonGroup
              disabled={checkIfNotInStock(item.product_stock)}
              variant='outlined'
              color='primary'
            >
              <Button onClick={() => handleQtyDecrease(item)}>-</Button>
              <Button>{item.quantity}</Button>
              <Button onClick={() => handleQtyIncrease(item)}>+</Button>
            </ButtonGroup>
            <IconButton onClick={() => removeProduct(item)}>
              <i className='far fa-trash-alt'></i>
            </IconButton>
          </Grid>
        </Grid>
      </ListItem>
    ));
  };

  return (
    <div className={classes.container}>
      <div className={classes.cartItems}>
        <Typography variant='h5' gutterBottom>
          MY CART
        </Typography>
        <Divider style={{ marginBottom: '1rem' }} />
        <List>{showCartItems()}</List>
        <Divider style={{ margin: '1rem 0' }} />
        <Box display='flex' justifyContent='space-between' alignItems='center'>
          <Box display='flex'>
            <Typography
              variant='h6'
              color='textSecondary'
              style={{ marginRight: '1rem' }}
            >
              Subtotal:
            </Typography>
            <Typography variant='h6'>NPR. {Math.trunc(total)}</Typography>
          </Box>
          <Button
            color='primary'
            variant='contained'
            onClick={handleCartCheckout}
          >
            Checkout
          </Button>
        </Box>
      </div>
      <Button
        variant='contained'
        color='secondary'
        style={{ marginTop: '2rem' }}
        onClick={clearAllCartItems}
      >
        Clear Cart {`(${Math.trunc(itemCount)})`}
      </Button>
    </div>
  );
};

export default Cart;
