import React from 'react';
import Loading from '../components/loading';
import { useQuery } from 'react-query';
import getBankDetails from '../services/user/getBankDetails';
import { Container, Box, makeStyles } from '@material-ui/core';
import { Typography } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  qrImage: {
    width: '50%',
    [theme.breakpoints.down('xs')]: {
      width: '90%',
    },
  },
}));

const BankDetails = () => {
  const { isLoading, data } = useQuery('bank_details', getBankDetails);
  const classes = useStyles();

  if (isLoading) {
    return <Loading />;
  }

  return (
    <Container>
      <Box margin='3rem 0'>
        <Typography variant='h4' gutterBottom>
          BANK DETAILS
        </Typography>
        <Typography gutterBottom variant='h6'>
          Account Name: {data[0].account_name}
        </Typography>

        <Typography gutterBottom variant='h6'>
          Account Number: {data[0].account_number}
        </Typography>
        <Typography gutterBottom variant='h6'>
          Branch: {data[0].branch}
        </Typography>
        <Typography gutterBottom variant='h6'>
          Esewa Name: {data[0].esewa_name}
        </Typography>
        <Typography gutterBottom variant='h6'>
          Esewa Number: {data[0].esewa_number}
        </Typography>

        <Box margin='3rem 0'>
          <img
            alt='Bank Details'
            src={`${process.env.REACT_APP_UPLOAD_URL}${data[0].qr_code}`}
            className={classes.qrImage}
          />
        </Box>
      </Box>
    </Container>
  );
};

export default BankDetails;
