import React, { useContext, useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import {
  makeStyles,
  AppBar,
  Toolbar,
  Badge,
  Menu,
  MenuItem,
  Button,
  Avatar,
} from '@material-ui/core';

import SearchBar from './searchBar';
import { CartContext } from '../../contexts/CartContext';
import { UserContext } from '../../contexts/UserContext';
import headerLogo from '../../assets/header_logo.png';

const useStyles = makeStyles((theme) => ({
  topBar: {
    marginBottom: '6px',
    backgroundColor: 'white',
  },
  logoContainer: {
    width: '70%',
    [theme.breakpoints.down('xs')]: {
      width: '56%',
    },
  },
  logo: {
    height: '40px',
    width: '230px',
    [theme.breakpoints.down('xs')]: {
      width: '100%',
    },
  },
  toolbar: {
    padding: '0 5rem',
    [theme.breakpoints.down('sm')]: {
      padding: '0 1rem',
    },
  },
  navItems: {
    display: 'flex',
    alignItems: 'center',
  },
  cartLink: {
    fontSize: '1.2rem',
    margin: '0 1rem',
    [theme.breakpoints.down('xs')]: {
      marginRight: '10px',
    },
  },
  smallAvatar: {
    width: theme.spacing(4),
    height: theme.spacing(4),
    backgroundColor: 'white',
    [theme.breakpoints.down('xs')]: {
      marginLeft: '1.5rem',
    },
    color: theme.palette.primary.main,
  },
  search: {
    [theme.breakpoints.down('sm')]: {
      display: 'none',
    },
  },
  login_profile: {
    marginLeft: '10px',
  },
}));

const Header = () => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = useState(null);
  const isMenuOpen = Boolean(anchorEl);
  const history = useHistory();

  const { itemCount } = useContext(CartContext);
  const [UserState, dispatch] = useContext(UserContext);

  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    handleMenuClose();
    dispatch({ type: 'LOGOUT' });
    localStorage.removeItem('refresh');
    localStorage.removeItem('access');
    localStorage.removeItem('user');
  };

  //menue to open when hovered on user avatar
  const menuId = 'primary-account-menu';
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <Link to='/profile'>
        <MenuItem dense={true} onClick={handleMenuClose}>
          YOUR PROFILE
        </MenuItem>
      </Link>
      <Link to='/public-profile'>
        <MenuItem dense={true} onClick={handleMenuClose}>
          YOUR PUBLIC PROFILE
        </MenuItem>
      </Link>
      <MenuItem dense={true} onClick={handleLogout}>
        LOGOUT
      </MenuItem>
    </Menu>
  );

  return (
    <AppBar
      position='sticky'
      color='default'
      elevation={0}
      className={classes.topBar}
    >
      <Toolbar className={classes.toolbar}>
        <div
          className={classes.logoContainer}
          onClick={() => history.push('/')}
          style={{ cursor: 'pointer' }}
        >
          <img src={headerLogo} className={classes.logo} alt='logo' />
        </div>
        <div className={classes.navItems}>
          <div className={classes.search}>
            <SearchBar />
          </div>
          <div>
            <Link to='/my-favourites' className={classes.smallAvatar}>
              <i className='far fa-heart' style={{ fontSize: '1.3rem' }}></i>
            </Link>
          </div>
          <div>
            <Link to='/cart' className={classes.cartLink}>
              <Badge
                badgeContent={Number(itemCount).toFixed(0)}
                color='secondary'
                aria-label='account of current user'
                aria-controls='primary-account-menu'
                aria-haspopup='true'
              >
                <i className='fas fa-shopping-cart'></i>
              </Badge>
            </Link>
          </div>
          <div className={classes.login_profile}>
            {UserState.user ? (
              <div>
                <Avatar
                  alt='Remy Sharp'
                  src=''
                  onClick={handleMenuOpen}
                  className={classes.smallAvatar}
                  style={{ border: '1px solid #caae53' }}
                />
              </div>
            ) : (
              <Link to='/signin'>
                <Button variant='outlined'>Login</Button>
              </Link>
            )}
          </div>
        </div>
      </Toolbar>
      {renderMenu}
    </AppBar>
  );
};

export default Header;
