import { Container, Typography } from '@material-ui/core';
import React from 'react';
import { useQuery } from 'react-query';
import Loading from '../components/loading';
import about from '../services/kbs/about';

const Delivery = () => {
  const { isLoading, data } = useQuery('about', about);

  if (isLoading) {
    return <Loading />;
  }

  return (
    <Container>
      <div style={{ minHeight: '70vh' }}>
        <Typography variant='h4' align='center' style={{ margin: '3rem 0' }}>
          DELIVERY DETAILS
        </Typography>

        <Typography gutterBottom>{data[0].delivery_charges}</Typography>
        <Typography variant='h6' gutterBottom>
          {data[0].inside_valley}
        </Typography>
        <Typography variant='h6' gutterBottom>
          {data[0].outside_valley}
        </Typography>
      </div>
    </Container>
  );
};

export default Delivery;
