import React from 'react';
import { useQuery } from 'react-query';
import { Typography, makeStyles } from '@material-ui/core';
import ProductCard from './productCard';
import getProducts from '../services/products/getProducts';
import Loading from './loading';
import getAd from '../services/products/getAd';
import AdvertisementCard from './advertisementCard';

const useStyles = makeStyles((theme) => ({
  newArrivals: {
    padding: '2rem 2rem 1rem 2rem',
    [theme.breakpoints.down('xs')]: {
      padding: '10px',
    },
  },
  newArrivals_items: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
    '& > *': {
      margin: '10px',
    },
  },
}));

const NewArrivals = () => {
  const classes = useStyles();

  const { isLoading, isError, data, error } = useQuery('products', getProducts);

  const {
    isLoading: isAdLoading,
    isError: isAdError,
    data: adData,
    error: adError,
  } = useQuery('ad', getAd);

  const showAdvertisementCard = () => {
    if (isAdLoading) {
      return <Loading />;
    }
    if (isAdError) {
      console.log(adError);
      return;
    }
    return adData.map((item) => (
      <AdvertisementCard
        title={item.ad_title}
        subtitle={item.ad_desc}
        id={item.id}
        image={item.image}
        products={item.products}
      />
    ));
  };

  if (isLoading) {
    return <Loading />;
  }

  if (isError) {
    return <h1>{error}</h1>;
  }

  return (
    <div className={classes.newArrivals}>
      <Typography
        color='textSecondary'
        variant='h5'
        gutterBottom
        align='center'
        style={{ fontWeight: 'bold' }}
      >
        NEW ARRIVALS
      </Typography>
      <div className={classes.newArrivals_items}>
        {data &&
          data.map((product) => {
            return (
              <ProductCard
                key={product.id}
                name={product.product_name}
                subtitle={product.description}
                image={`${process.env.REACT_APP_UPLOAD_URL}${product.images[0].image}`}
                price={product.price}
                discount={product.discount_amount}
                id={product.id}
                bestSeller={product.best_seller}
                brandName={product.brand && product.brand.brand_name}
              />
            );
          })}
        {showAdvertisementCard()}
      </div>
    </div>
  );
};

export default NewArrivals;
