import React from 'react';
import { Box, makeStyles, Typography, Button } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import SkinCareRoutineCard from './skinCareRoutineCard';
import { useQuery } from 'react-query';
import getSkinCareRoutines from '../services/user/getSkinCareRoutines';
import Loading from './loading';

const useStyles = makeStyles((theme) => ({
  container: {
    backgroundColor: '#fafafa',
    padding: '2rem',
  },
}));

const SkinCareRoutines = () => {
  const classes = useStyles();
  const history = useHistory();

  const { isLoading, data } = useQuery(
    'skincare-routines',
    getSkinCareRoutines
  );

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div className={classes.container}>
      <Typography
        color='textSecondary'
        variant='h5'
        gutterBottom
        align='center'
        style={{ fontWeight: 'bold' }}
      >
        SKINCARE ROUTINES
      </Typography>
      <Typography
        color='textSecondary'
        variant='subtitle2'
        gutterBottom
        align='center'
      >
        Share your skincare routine to help others figure out theirs!
      </Typography>
      <Box
        display='flex'
        flexWrap='wrap'
        justifyContent='space-evenly'
        alignItems='stretch'
      >
        {data.map((item) => (
          <SkinCareRoutineCard
            key={item.image}
            userName={item.first_name + ' ' + item.last_name}
            skinType={item.skin_type}
            morning={item.morning_skincare_routine}
            evening={item.evening_skincare_routine}
            image={item.image}
          />
        ))}
      </Box>
      <Box textAlign='center'>
        <Button
          style={{ marginTop: '1rem' }}
          color='primary'
          variant='outlined'
          onClick={() => history.push('/skincare-routines')}
        >
          See All
        </Button>
      </Box>
    </div>
  );
};

export default SkinCareRoutines;
