import React from 'react';
import { Box, Typography, Button, Grid, makeStyles } from '@material-ui/core';
import { useQuery } from 'react-query';
import getUserInfo from '../services/user/getUserInfo';
import Loading from './loading';

const useStyles = makeStyles((theme) => ({
  infoLg: {
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
  infoSm: {
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
}));

const PersonalInfo = () => {
  const classes = useStyles();

  const { isLoading, data } = useQuery('userInfo', getUserInfo);

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div style={{ padding: '10px' }}>
      <Box display='flex' justifyContent='space-between'>
        <Typography variant='h6'>Personal Information</Typography>
        <Button variant='outlined' color='primary' size='small'>
          Edit info
        </Button>
      </Box>
      <Grid container style={{ marginTop: '2rem' }} justify='space-between'>
        <Grid md={4} sm={12} item>
          <Typography gutterBottom variant='h6'>
            Name :{' '}
            <Typography
              gutterBottom
              variant='h6'
              color='textSecondary'
              className={classes.infoSm}
            >
              Aaliyah Gautam
            </Typography>
          </Typography>
          <Typography gutterBottom variant='h6'>
            Email :{' '}
            <Typography
              gutterBottom
              variant='h6'
              color='textSecondary'
              className={classes.infoSm}
            >
              aaliyah2@gmail.com
            </Typography>
          </Typography>
          <Typography gutterBottom variant='h6'>
            Password :{' '}
            <Typography
              gutterBottom
              variant='h6'
              color='textSecondary'
              className={classes.infoSm}
            >
              ********
            </Typography>
          </Typography>
        </Grid>
        <Grid md={4} className={classes.infoLg} item>
          <Typography gutterBottom variant='h6' color='textSecondary'>
            {data[0].first_name} {data[0].last_name}
          </Typography>
          <Typography gutterBottom variant='h6' color='textSecondary'>
            {data[0].email}
          </Typography>
          <Typography gutterBottom variant='h6' color='textSecondary'>
            ********
          </Typography>
        </Grid>
      </Grid>
    </div>
  );
};

export default PersonalInfo;
