import { Typography, Container, Box } from '@material-ui/core';

import FavProductCard from '../components/favProductCard';

const Favourite = () => {
  let favItems = JSON.parse(localStorage.getItem('fav'));

  const showFavProducts = () => {
    if (!favItems) {
      return <Typography variant='h5'>No favourities are added</Typography>;
    }
    return favItems.map((item) => {
      return (
        <Box key={item.productId}>
          <FavProductCard
            name={item.productName}
            id={item.productId}
            brandName={item.productBrand}
            price={item.productPrice}
            image={`${process.env.REACT_APP_UPLOAD_URL}${item.productImage}`}
          />
        </Box>
      );
    });
  };

  return (
    <div style={{ minHeight: '70vh' }}>
      <Container>
        <Typography
          variant='h4'
          gutterBottom
          style={{ margin: '2.5rem 0' }}
          align='center'
        >
          My Favourites
        </Typography>
        <Box
          display='flex'
          flexWrap='wrap'
          justifyContent='space-between'
          style={{ position: 'relative' }}
        >
          {showFavProducts()}
        </Box>
      </Container>
    </div>
  );
};

export default Favourite;
