import {
  Box,
  Button,
  ButtonGroup,
  Chip,
  Divider,
  Grid,
  IconButton,
  makeStyles,
  Typography,
} from '@material-ui/core';
import { Rating } from '@material-ui/lab';
import React, { useState, useContext } from 'react';
import { useHistory } from 'react-router';
import { toast } from 'react-toastify';
import { CartContext } from '../contexts/CartContext';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { useMutation } from 'react-query';
import notifyStock from '../services/products/notifyStock';

const useStyles = makeStyles((theme) => ({
  previewImages: {
    marginTop: '1rem',
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
  },
  previewImages_item: {
    width: '70px',
    margin: '0 10px',
    borderRadius: '50%',
    objectFit: 'contain',
    cursor: 'pointer',
    border: `1px solid ${theme.palette.primary.main}`,
    marginTop: '10px',
  },
  image: {
    width: '400px',
    [theme.breakpoints.down('xs')]: {
      width: '300px',
    },
    transition: 'transform .2s',
    '&:hover, &:focus': {
      transform: 'scale(1.7)',
    },
  },
  productDesc: {
    fontSize: '0.9rem',
    marginTop: '3rem',
    padding: '0 7rem',
    [theme.breakpoints.down('xs')]: {
      padding: '0 1rem',
    },
  },
}));

const ProductInfo = ({ images, details, othersizes }) => {
  const [imageNumber, setImageNumber] = useState(0);
  const [imageSrc, setImageSrc] = useState(images[0].image);
  let [quantity, setQuantity] = useState(1);
  const { addProduct } = useContext(CartContext);
  const classes = useStyles();
  const history = useHistory();

  const mutation = useMutation((product) => notifyStock(product));

  const handleNotify = (product_id) => {
    mutation.mutate(
      { product_id: product_id },
      {
        onSuccess: () => {
          toast.info('You will be notified');
        },
        onError: (err) => {
          console.log('err', err);
          toast.info('Please login');
        },
      }
    );
  };

  const showPreviousImage = () => {
    if (imageNumber === 0) {
      return;
    }
    let previousImage = imageNumber - 1;
    setImageNumber(previousImage);
    setImageSrc(images[imageNumber].image);
  };

  const showNextImage = () => {
    if (imageNumber >= images.length - 1) {
      return;
    }
    let nextImage = imageNumber + 1;
    setImageNumber(nextImage);
    setImageSrc(images[imageNumber].image);
  };

  const checkIfNotInStock = (productCount) => {
    if (productCount <= 0) {
      return true;
    } else {
      return false;
    }
  };

  const handleQtyIncrease = () => {
    if (quantity > details.product_stock - 2) {
      return;
    }
    setQuantity(quantity++);
  };

  const handleQtyDecrease = () => {
    if (quantity === 1) {
      setQuantity(1);
      return;
    }
    setQuantity(quantity--);
  };

  const showImagesPreview = (images) => {
    return images.map((img) => (
      <img
        src={`${process.env.REACT_APP_UPLOAD_URL}${img.image}`}
        alt={img.id}
        key={img.id}
        className={classes.previewImages_item}
        onClick={() => setImageSrc(img.image)}
      />
    ));
  };

  const addToFavouriteProduct = (product) => {
    const prevProducts = JSON.parse(localStorage.getItem('fav'));

    if (!prevProducts) {
      localStorage.setItem(
        'fav',
        JSON.stringify([
          {
            productId: product.id,
            productName: product.product_name,
            productImage: product.images[0].image,
            productBrand: product.brand && product.brand.brand_name,
            productPrice: product.price,
            bestSeller: product.bestSeller,
          },
        ])
      );
      toast.info('Product addded to favourites');
      return;
    }

    const isAlreadyFav = prevProducts.find(
      (fav) => product.id === fav.productId
    );

    if (isAlreadyFav) {
      return;
    }

    localStorage.setItem('fav', [
      JSON.stringify([
        ...prevProducts,
        {
          productId: product.id,
          productName: product.product_name,
          productImage: product.images[0].image,
          productBrand: product.brand && product.brand.brand_name,
          productPrice: product.price,
          bestSeller: product.bestSeller,
        },
      ]),
    ]);
    toast.info('Product addded to favourites');
  };

  const showOtherSizes = () => {
    if (othersizes.length === 0) {
      return <Typography>Not available</Typography>;
    } else {
      return othersizes.map((size) => (
        <Box key={size.id}>
          <Button
            variant='outlined'
            color='primary'
            style={{ marginBottom: '10px' }}
            endIcon={<ShoppingCartIcon />}
            onClick={() => {
              const product = {
                ...size,
                brand: { brand_name: details.brand.brand_name },
                images: details.images,
                quantity: quantity,
              };
              addProduct(product);
              toast.dark('Product successfully added to cart!', {});
            }}
          >
            {size.product_size}
          </Button>
          <Typography
            varaint='subtitle'
            component='span'
            style={{
              color: '#808080',
              fontWeight: 'bold',
              fontSize: '0.9rem',
              textDecoration: 'line-through',
              paddingLeft: '10px',
            }}
          >
            {'  '}
            NPR. {size.price}
          </Typography>
          <Typography
            varaint='subtitle'
            component='span'
            style={{
              color: '#808080',
              fontWeight: 'bold',
              fontSize: '0.9rem',
              paddingLeft: '10px',
            }}
          >
            {'  '}
            NPR. {size.price - size.discount_amount}
          </Typography>
        </Box>
      ));
    }
  };

  return (
    <>
      <Grid container spacing={2} alignItems='center' justify='center'>
        <Grid item md={5}>
          <Box display='flex' alignItems='center' justifyContent='center'>
            <span
              style={{ cursor: 'pointer', fontSize: '2rem' }}
              onClick={showPreviousImage}
            >
              <i className='fas fa-chevron-left'></i>
            </span>
            <img
              src={`${process.env.REACT_APP_UPLOAD_URL}${imageSrc}`}
              alt={images[0].id}
              className={classes.image}
            />
            <span
              style={{ cursor: 'pointer', fontSize: '2rem' }}
              onClick={showNextImage}
            >
              <i className='fas fa-chevron-right'></i>
            </span>
          </Box>
          <div className={classes.previewImages}>
            {showImagesPreview(images)}
          </div>
        </Grid>
        <Grid item md={5}>
          <Box>
            <Box display='flex'>
              <Typography
                variant='h5'
                gutterBottom
                style={{
                  fontWeight: 'bold',
                  cursor: 'pointer',
                  textDecoration: 'underline',
                }}
                onClick={() => history.push(`/brand/${details.brand.id}`)}
              >
                {details.brand && details.brand.brand_name}
              </Typography>
              {details.best_seller && (
                <Chip
                  style={{
                    color: 'white',
                    marginLeft: '1rem',
                  }}
                  color='primary'
                  label='Best Seller'
                />
              )}
            </Box>
            {details.product_size ? (
              <Typography variant='h6' gutterBottom>
                {details.product_name}
                <Typography varaint='subtitle' component='span' gutterBottom>
                  {' '}
                  ( {details.product_size} )
                </Typography>
              </Typography>
            ) : (
              <Typography variant='h6' gutterBottom>
                {details.product_name}
              </Typography>
            )}
            {details.discount_amount === 0 ? (
              <Typography
                variant='h5'
                color='primary'
                style={{ fontWeight: 'bold' }}
                gutterBottom
              >
                NPR. {details.price}
              </Typography>
            ) : (
              <>
                <Box display='flex' alignItems='center'>
                  <Typography
                    variant='h6'
                    color='primary'
                    style={{
                      textDecoration: 'line-through',
                      fontSize: '1.5rem',
                    }}
                    gutterBottom
                  >
                    NPR. {details.price}
                  </Typography>
                  <Typography
                    variant='h6'
                    color='secondary'
                    style={{ marginBottom: '12px', marginLeft: '10px' }}
                  >
                    ( NRP.{details.discount_amount} off )
                  </Typography>
                </Box>
                <Typography
                  variant='h5'
                  color='primary'
                  style={{ fontWeight: 'bold' }}
                >
                  NPR. {details.price - details.discount_amount}
                </Typography>
              </>
            )}
            <Typography variant='subtitle2' gutterBottom>
              Inclusive of all taxes
            </Typography>
            <Box display='flex' style={{ marginBottom: '1rem' }}>
              <Rating
                value={details.average_rating}
                readOnly={true}
                precision={0.5}
                style={{ marginRight: '10px' }}
              />
              <Typography varaint='subtitle' component='span' gutterBottom>
                {details.review_count} reviews
              </Typography>
            </Box>

            <Box
              display='flex'
              alignItems='center'
              style={{ marginBottom: '1rem' }}
            >
              <Typography style={{ marginRight: '2rem' }}>QTY: </Typography>
              <ButtonGroup
                disabled={checkIfNotInStock(details.product_stock)}
                variant='outlined'
                color='primary'
              >
                <Button onClick={handleQtyDecrease}>-</Button>
                <Button>{quantity}</Button>
                <Button onClick={handleQtyIncrease}>+</Button>
              </ButtonGroup>
            </Box>
            <Box>
              <IconButton
                onClick={() => addToFavouriteProduct(details)}
                style={{
                  marginRight: '1rem',
                  marginBottom: '1rem',
                }}
              >
                <i className='far fa-heart'></i>
              </IconButton>
              <Button
                variant='contained'
                color='primary'
                disabled={checkIfNotInStock(details.product_stock)}
                onClick={() => {
                  const product = {
                    ...details,
                    quantity: quantity,
                  };
                  addProduct(product);
                  toast.dark('Product successfully added to cart!', {});
                }}
              >
                ADD TO CART
              </Button>
              {checkIfNotInStock(details.product_stock) && (
                <Typography
                  variant='h6'
                  color='primary'
                  style={{ cursor: 'pointer', textDecoration: 'underline' }}
                  onClick={() => handleNotify(details.id)}
                >
                  Notify me when in stock
                </Typography>
              )}
            </Box>
            <Box display='flex' style={{ marginTop: '1.5rem' }}>
              <Typography varaint='subtitle' component='span' gutterBottom>
                Other Sizes:
              </Typography>

              <Box style={{ marginBottom: '16px', marginLeft: '1rem' }}>
                {showOtherSizes()}
              </Box>
            </Box>
            <Typography color='secondary' variant='subtitle1'>
              100% Genuine Product
            </Typography>
          </Box>
        </Grid>
      </Grid>
      <div className={classes.productDesc}>
        <Typography style={{ fontWeight: 'bold' }} gutterBottom>
          Description
        </Typography>
        <Typography gutterBottom>{details.product_desc}</Typography>
        {details.how_to_use && (
          <>
            <Typography style={{ fontWeight: 'bold' }} gutterBottom>
              How to use
            </Typography>
            <Typography>{details.how_to_use}</Typography>
          </>
        )}
        <Divider style={{ margin: '1rem 0' }} />
      </div>
    </>
  );
};

export default ProductInfo;
