import React from 'react';
import { useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { Chip, IconButton } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';

const useStyles = makeStyles((theme) => ({
  root: {
    width: 300,
    [theme.breakpoints.down('xs')]: {
      minWidth: '90%',
    },
    margin: '1rem',
  },
}));

const FavProductCard = ({ name, price, image, id, brandName, bestSeller }) => {
  const classes = useStyles();
  const history = useHistory();

  let favItems = JSON.parse(localStorage.getItem('fav'));

  const deleteProductFromFav = (id) => {
    favItems = favItems.filter(function (value, index, arr) {
      return value.productId !== id;
    });
    localStorage.setItem('fav', JSON.stringify(favItems));
    window.location.reload();
  };

  return (
    <Card className={classes.root}>
      <CardActionArea
        onClick={() => {
          history.push(`/product/${id}`);
        }}
      >
        <CardMedia
          component='img'
          alt={name}
          height='190'
          style={{ objectFit: 'contain' }}
          src={image}
          title={name}
        />
        <CardContent>
          <Typography gutterBottom variant='h6' component='h2'>
            {name}
          </Typography>
          <Typography variant='subtitle1' color='primary' component='p'>
            {brandName}
          </Typography>
          {bestSeller && (
            <Chip
              style={{ position: 'absolute', top: 5, right: 5, color: 'white' }}
              color='primary'
              label='Best Seller'
            />
          )}
        </CardContent>
      </CardActionArea>
      <CardActions
        style={{
          display: 'flex',
          justifyContent: 'flex-end',
        }}
      >
        <Button size='small' color='primary'>
          NPR. {price}
        </Button>
        <IconButton
          size='small'
          color='secondary'
          variant='contained'
          onClick={() => deleteProductFromFav(id)}
        >
          <DeleteIcon />
        </IconButton>
      </CardActions>
    </Card>
  );
};

export default FavProductCard;
