import React, { useState, useContext, useEffect } from 'react';
import {
  Button,
  Divider,
  FilledInput,
  FormControl,
  InputAdornment,
  InputLabel,
  makeStyles,
  Typography,
} from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import axios from 'axios';
// import { GoogleLogin } from 'react-google-login';
// import FacebookLogin from 'react-facebook-login';
import { Link } from 'react-router-dom';
import { UserContext } from '../contexts/UserContext';

const useStyles = makeStyles((theme) => ({
  signin_container: {
    padding: '2rem 1rem',
    backgroundColor: 'white',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
  },
  logo: {
    display: 'block',
    width: '120px',
    margin: 'auto',
    marginTop: '2rem',
  },
  signup_form: {
    width: '40%',
    margin: '1rem auto',
    [theme.breakpoints.down('sm')]: {
      width: '90%',
    },
  },
  signup_form_item: {
    marginBottom: '1rem',
  },
  signin_oauth: {
    display: 'flex',
    flexWrap: 'wrap',
    marginTop: '1rem',
    justifyContent: 'space-evenly',
    width: '100%',
    marginBottom: '1.5rem',
  },
  oauth_btns: {
    flexBasis: '40%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '67%',
      marginBottom: '1rem',
    },
  },
}));

const Signin = () => {
  const [isDisabled, setIsDisabled] = useState(false);
  const [error, setError] = useState(null);
  const [UserState, dispatch] = useContext(UserContext);

  const history = useHistory();
  const classes = useStyles();

  useEffect(() => {
    if (UserState.user != null) {
      history.push('/');
    }
  }, [UserState.user, history]);

  // const responseGoogle = (response) => {
  //   console.log(response);
  // };

  // const responseFacebook = (response) => {
  //   console.log(response);
  // };

  const handleSignin = async (e) => {
    e.preventDefault();
    setIsDisabled(true);
    const email = e.target.email.value;
    const password = e.target.password.value;
    try {
      const response = await axios.post(
        `${process.env.REACT_APP_API_URL}/token/`,
        { email, password }
      );
      dispatch({
        type: 'LOGIN',
        payload: {
          access: response.data.access,
          refresh: response.data.refresh,
          user: response.data.user,
        },
      });
      localStorage.setItem('access', response.data.access);
      localStorage.setItem('refresh', response.data.refresh);
      localStorage.setItem('user', JSON.stringify(response.data.user));
      history.push('/');
    } catch (err) {
      setIsDisabled(false);
      setError(true);
      console.log(err.message);
    }
  };

  return (
    <div className={classes.signin_container}>
      <img src='0.png' alt='logo' className={classes.logo} />
      {error && (
        <Typography align='center' color='error' variant='h5'>
          Please provide valid credentials
        </Typography>
      )}
      <form className={classes.signup_form} onSubmit={handleSignin}>
        <FormControl
          variant='filled'
          color='secondary'
          fullWidth={true}
          className={classes.signup_form_item}
        >
          <InputLabel htmlFor='filled-adornment-email'>Enter Email</InputLabel>
          <FilledInput
            id='filled-adornment-email'
            type='email'
            margin='dense'
            name='email'
            style={{ backgroundColor: 'white' }}
            endAdornment={
              <InputAdornment position='end'>
                <i className='fas fa-envelope'></i>
              </InputAdornment>
            }
          />
        </FormControl>
        <FormControl
          variant='filled'
          color='secondary'
          fullWidth={true}
          className={classes.signup_form_item}
        >
          <InputLabel htmlFor='filled-adornment-password'>
            Enter Password
          </InputLabel>
          <FilledInput
            id='filled-adornment-password'
            type='password'
            margin='dense'
            name='password'
            style={{ backgroundColor: 'white' }}
            endAdornment={
              <InputAdornment position='end'>
                <i className='fas fa-key'></i>
              </InputAdornment>
            }
          />
        </FormControl>
        <Button
          variant='contained'
          color='primary'
          fullWidth
          style={{ marginBottom: '1rem' }}
          type='submit'
          disabled={isDisabled}
        >
          Sign In
        </Button>
        <div style={{ marginBottom: '1rem' }}>
          <Typography align='center' variant='subtitle2' gutterBottom>
            Don't have an account ?{' '}
            <Link
              to='/signup'
              style={{ fontSize: '1.1rem', color: '#f9a825' }}
              color='primary'
            >
              Register
            </Link>{' '}
          </Typography>
          <Typography align='center' variant='subtitle1' color='primary'>
            <Link to='/forgot-password' color='primary'>
              Forgot password ?
            </Link>{' '}
          </Typography>
        </div>
        <Divider variant='middle' />
        {/* <div className={classes.signin_oauth}>
          <GoogleLogin
            clientId='8HQUnTWvyEWrnzFhAtciCDtNWq46dqFczwJFIZyc'
            buttonText='LOGIN WITH GOOGLE'
            onSuccess={responseGoogle}
            onFailure={responseGoogle}
            cookiePolicy={'single_host_origin'}
            className={classes.oauth_btns}
          />
          <FacebookLogin
            appId='1088597931155576'
            autoLoad={false}
            fields='name,email,picture'
            callback={responseFacebook}
          />
        </div> */}
      </form>
    </div>
  );
};

export default Signin;
