import React from 'react';
import {
  Drawer,
  List,
  ListItem,
  ListItemText,
  Container,
  Divider,
  Button,
} from '@material-ui/core';
import { Link, useHistory } from 'react-router-dom';
import { useQuery } from 'react-query';
import getCategories from '../../services/products/getCategories';

const DrawerComponent = ({ isOpen, onClose }) => {
  const {
    isLoading: isCategoriesLoading,
    isError: isCategoriesError,
    data: categories,
    error: categoriesError,
  } = useQuery('categories', getCategories);
  const history = useHistory();

  const showSubCategories = (subcategories) => {
    if (!subcategories) {
      return;
    }
    return subcategories.map((subcategory) => (
      <Link
        style={{ display: 'block', fontSize: '0.8rem' }}
        to={`/sub_category/${subcategory.sub_id}`}
        key={subcategory.sub_category}
      >
        {subcategory.sub_category}
      </Link>
    ));
  };

  if (isCategoriesError) {
    console.log(categoriesError);
  }

  const showProductCategroies = () => {
    return categories.map((category) => (
      <ListItem key={category.id}>
        <ListItemText onClick={onClose}>
          <Link to={`/category/${category.id}`}>{category.categories}</Link>
          {category.subcategory.length > 0 &&
            showSubCategories(category.subcategory)}
        </ListItemText>
      </ListItem>
    ));
  };

  return (
    <Drawer anchor='left' open={isOpen} onClose={onClose}>
      <Container>
        <Button
          variant='contained'
          color='primary'
          size='large'
          style={{ marginTop: '2rem' }}
          fullWidth
          onClick={() => history.push('/brands')}
        >
          VIEW ALL BRANDS
        </Button>
        <List>
          {isCategoriesLoading === true ? (
            <h3>Loading</h3>
          ) : (
            showProductCategroies()
          )}
        </List>
        <Divider />
      </Container>
    </Drawer>
  );
};

export default DrawerComponent;
