import { Typography } from '@material-ui/core';
import { Button } from '@material-ui/core';
import { Container, FilledInput, Box, InputAdornment } from '@material-ui/core';
import axios from 'axios';
import React, { useState } from 'react';
import { toast } from 'react-toastify';
import { useHistory, useLocation } from 'react-router-dom';

const ResetPassword = () => {
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const { search } = useLocation();
  const history = useHistory();

  console.log(search.split('=')[1]);

  const handleSubmit = async () => {
    if (password === confirmPassword) {
      const response = await axios.post(
        `http://kadariyabeautystation.com/accounts/password/reset/verified/`,
        { password, code: search.split('=')[1] }
      );
      if (response.status === 200) {
        history.push('/signin');
      }
    } else {
      toast.error("password doesn't match");
      return;
    }
  };

  return (
    <Container style={{ minHeight: '65vh' }}>
      <Typography variant='h5' align='center' style={{ margin: '2rem 0' }}>
        Please enter new password
      </Typography>
      <Box textAlign='center'>
        <FilledInput
          id='filled-adornment-email'
          type='password'
          margin='dense'
          name='newPassword'
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          style={{ backgroundColor: 'white' }}
          placeholder='enter new password'
          endAdornment={
            <InputAdornment position='end'>
              <i className='fas fa-key'></i>
            </InputAdornment>
          }
        />
      </Box>
      <Box textAlign='center'>
        <FilledInput
          id='filled-adornment-email'
          type='password'
          margin='dense'
          name='confirmPassword'
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
          style={{ backgroundColor: 'white' }}
          placeholder='confirm password'
          endAdornment={
            <InputAdornment position='end'>
              <i className='fas fa-key'></i>
            </InputAdornment>
          }
        />
      </Box>
      <Box textAlign='center' style={{ marginTop: '2rem' }}>
        <Button onClick={handleSubmit} variant='contained' color='primary'>
          Reset Password
        </Button>
      </Box>
    </Container>
  );
};

export default ResetPassword;
