import { Select } from '@material-ui/core';
import { Button, IconButton } from '@material-ui/core';
import { makeStyles, Typography, TextareaAutosize } from '@material-ui/core';
import React from 'react';
import { useMutation, useQueryClient } from 'react-query';
import createUserProfile from '../services/user/createUserProfile';

const useStyles = makeStyles((theme) => ({
  modal: {
    width: '60vw',
    height: 'fit-content',
    minHeight: '300px',
    backgroundColor: theme.palette.primary.dark,
    color: 'white',
    margin: 'auto',
    textAlign: 'center',
    padding: '1rem',
    position: 'absolute',
    top: '20%',
    [theme.breakpoints.down('xs')]: {
      width: '90%',
    },
  },
  closeBtn: {
    position: 'absolute',
    top: '0',
    right: '0',
  },
}));

const EditPublicProfile = ({ setShowEditProfile }) => {
  const classes = useStyles();

  const mutation = useMutation((profileInfo) => createUserProfile(profileInfo));
  const queryClient = useQueryClient();

  const handleSubmit = (e) => {
    e.preventDefault();
    mutation.mutate(
      {
        skin_type: e.target.skinType.value,
        morning_skincare_routine: e.target.morningRoutine.value,
        evening_skincare_routine: e.target.eveningRoutine.value,
      },
      {
        onSuccess: () => {
          queryClient.invalidateQueries('public-profile');
        },
      }
    );
    setShowEditProfile(false);
  };

  return (
    <div className={classes.modal}>
      <Typography variant='h5' gutterBottom>
        Edit Your Public Profile
      </Typography>
      <form onSubmit={handleSubmit}>
        <TextareaAutosize
          name='morningRoutine'
          placeholder='Enter morning skincare routine'
          style={{ padding: '10px', width: '90%' }}
          rowsMin={3}
        />

        <TextareaAutosize
          name='eveningRoutine'
          placeholder='Enter evening skincare routine'
          style={{ padding: '10px', width: '90%' }}
          rowsMin={3}
        />

        <div style={{ marginTop: '1rem' }}>
          <Typography gutterBottom>Select Your Skin Type</Typography>
          <Select
            style={{
              backgroundColor: 'white',
              width: '90%',
            }}
            inputProps={{
              name: 'skinType',
            }}
          >
            <option style={{ padding: '5px', cursor: 'pointer' }} value='Dry'>
              Dry
            </option>
            <option style={{ padding: '5px', cursor: 'pointer' }} value='Oily'>
              Oily
            </option>
            <option
              style={{ padding: '5px', cursor: 'pointer' }}
              value='Combination'
            >
              Combination
            </option>
          </Select>
        </div>

        <Button variant='contained' style={{ marginTop: '1rem' }} type='submit'>
          Submit
        </Button>
      </form>
      <IconButton
        className={classes.closeBtn}
        onClick={() => setShowEditProfile(false)}
      >
        <i className='fas fa-window-close'></i>
      </IconButton>
    </div>
  );
};

export default EditPublicProfile;
