import React from 'react';
import { useQuery } from 'react-query';
import { Typography, makeStyles, Button, Container } from '@material-ui/core';
import ProductCard from './productCard';

import getProductByCategory from '../services/products/getProductByCategory';
import { useHistory } from 'react-router';
import Loading from './loading';

const useStyles = makeStyles((theme) => ({
  newArrivals_container: {
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    marginTop: '1rem',
  },
  newArrivals_items: {
    display: 'flex',
    flexWrap: 'wrap',
    padding: '2rem 0',
    justifyContent: 'space-evenly',

    '& > *': {
      margin: '10px',
    },
  },
}));

const CardContainerWithBackgroundImage = ({ category }) => {
  const classes = useStyles();

  const history = useHistory();

  const { isLoading, isError, data, error } = useQuery(`${category}`, () =>
    getProductByCategory(category.id)
  );

  if (isLoading) {
    return <Loading />;
  }

  if (isError) {
    return <h1>{error}</h1>;
  }

  if (data && data.length === 0) {
    return (
      <Container>
        <Typography variant='h5'>No products currently</Typography>
      </Container>
    );
  }

  return (
    <div style={{ marginBottom: '2rem' }}>
      <Typography
        color='textSecondary'
        variant='h5'
        gutterBottom
        align='center'
        style={{ fontWeight: 'bold', marginTop: '1rem' }}
      >
        {data[0].categorization[0].category_name.toUpperCase()}
      </Typography>

      <div
        className={classes.newArrivals_container}
        style={{
          backgroundImage: `url(./back.jpg)`,
          paddingBottom: '1rem',
        }}
      >
        <div className={classes.newArrivals_items}>
          {data &&
            data.map((product) => (
              <ProductCard
                key={product.id}
                name={product.product_name}
                subtitle={product.product_desc}
                image={`${process.env.REACT_APP_UPLOAD_URL}${product.images[0].image}`}
                brandName={product.brand && product.brand.brand_name}
                price={product.price}
                id={product.id}
                discount={product.discount_amount}
              />
            ))}
        </div>
        <Button
          color='primary'
          disableElevation
          variant='contained'
          style={{
            display: 'block',
            width: '100px',
            margin: 'auto',
          }}
          onClick={() => history.push(`/category/${category.id}`)}
        >
          See All
        </Button>
      </div>
    </div>
  );
};

export default CardContainerWithBackgroundImage;
