import React, { useState } from 'react';
import { makeStyles, AppBar, Toolbar, Typography } from '@material-ui/core';
import { Link as RouterLink } from 'react-router-dom';
import { useQuery } from 'react-query';

import SearchBar from './searchBar';
import DrawerComponent from './drawer';
import getCategories from '../../services/products/getCategories';

const useStyles = makeStyles((theme) => ({
  navbar: {
    padding: '0 3.5rem',
    borderRadius: '2%',
    backgroundColor: theme.palette.primary.main,
    [theme.breakpoints.down('sm')]: {
      padding: '0 10px',
    },
  },
  navItems: {
    width: '100%',
    display: 'flex',
    color: 'white',
    alignItems: 'center',
    justifyContent: 'space-between',
    [theme.breakpoints.down('sm')]: {
      display: 'none',
    },
  },
  navItems_item: {
    '&:hover': {
      color: 'black',
      cursor: 'pointer',
    },
  },
  navbarSm: {
    display: 'none',
    [theme.breakpoints.down('sm')]: {
      display: 'flex',
      width: '100%',
      justifyContent: 'space-between',
      alignItems: 'center',
    },
  },
}));

const Navbar = () => {
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
  const classes = useStyles();

  const { isLoading, isError, data } = useQuery('categories', getCategories);

  const renderNavLinks = () => {
    return isLoading ? (
      <Typography variant='h5' style={{ color: 'white' }}>
        Welcome to Kadariya Beauty Station
      </Typography>
    ) : isError ? (
      <h3>hello you shouldnt be seeing this</h3>
    ) : (
      <>
        {data.map((category) => (
          <Typography
            variant='subtitle2'
            className={classes.navItems_item}
            key={category.categories}
          >
            <RouterLink to={`/category/${category.id}`}>
              {category.categories.toUpperCase()}
            </RouterLink>
          </Typography>
        ))}
      </>
    );
  };

  if (data && data.length === 0) {
    return (
      <Typography variant='h6' style={{ padding: 10 }}>
        We are currenltly unavailable. Sorry for your inconvinience.
      </Typography>
    );
  }

  return (
    <AppBar position='static' elevation={5} className={classes.navbar}>
      <Toolbar variant='dense'>
        <div className={classes.navItems}>
          <Typography
            variant='subtitle2'
            className={classes.navItems_item}
            onClick={() => setIsDrawerOpen(true)}
          >
            BROWSE{' '}
            <span style={{ marginLeft: '10px' }}>
              <i className='fas fa-chevron-down'></i>
            </span>
          </Typography>

          {renderNavLinks()}
        </div>
        <div className={classes.navbarSm}>
          <Typography variant='subtitle2' onClick={() => setIsDrawerOpen(true)}>
            BROWSE{' '}
            <span style={{ marginLeft: '10px', cursor: 'pointer' }}>
              <i className='fas fa-bars'></i>
            </span>
          </Typography>
          <SearchBar />
        </div>
      </Toolbar>
      <DrawerComponent
        isOpen={isDrawerOpen}
        onClose={() => setIsDrawerOpen(false)}
      />
    </AppBar>
  );
};

export default Navbar;
