import React from 'react';

import { Card, Typography, Button, makeStyles } from '@material-ui/core';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {
    width: 400,
    minHeight: 310,
    marginTop: '4rem',
    marginBottom: '2.5rem',
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    color: 'white',
    [theme.breakpoints.down('xs')]: {
      width: 300,
    },
    position: 'relative',
  },
  backdrop: {
    position: 'absolute',
    top: 0,
    width: '100%',
    height: '100%',
    backgroundColor: 'black',
    opacity: 0.7,
    padding: 10,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-end',
  },
}));

const AdvertisementCard = ({ title, subtitle, image, id, products }) => {
  const classes = useStyles();
  const history = useHistory();

  return (
    <Card
      className={classes.root}
      style={{
        backgroundImage: `url(${process.env.REACT_APP_UPLOAD_URL}${image})`,
      }}
    >
      <div className={classes.backdrop}>
        <Typography variant='h5' gutterBottom>
          {title}
        </Typography>
        <Typography variant='subtitle1' gutterBottom>
          {subtitle}
        </Typography>
        <Button
          color='primary'
          variant='contained'
          style={{ width: '150px' }}
          onClick={() =>
            history.push(`/ad_products/${id}`, { products: products })
          }
          size='large'
        >
          Shop Now
        </Button>
      </div>
    </Card>
  );
};

export default AdvertisementCard;
