import React, { useState } from 'react';
import {
  Box,
  makeStyles,
  Paper,
  Avatar,
  Typography,
  Button,
} from '@material-ui/core';
import { useQuery, useQueryClient } from 'react-query';
import getPublicProfile from '../services/user/getPublicProfile';
import Loading from '../components/loading';
import { Divider } from '@material-ui/core';
import EditPublicProfile from '../components/editPublicProfile';
import api from '../services/api';

const useStyles = makeStyles((theme) => ({
  profileContainer: {
    width: '70%',
    padding: '1rem',
    margin: '3rem 0',
    [theme.breakpoints.down('xs')]: {
      width: '90%',
    },
  },
}));

const PublicProfile = () => {
  const classes = useStyles();

  const [showEditProfile, setShowEditProfile] = useState(false);
  const [picture, setPicture] = useState();
  const queryClient = useQueryClient();

  const { isLoading, data } = useQuery('public-profile', getPublicProfile);

  const handleProfilePictureSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append('image', picture);

    try {
      const data = await api.put(
        `/main/user-public-profile/${
          JSON.parse(localStorage.getItem('user')).user_id
        }/`,
        formData,
        {
          headers: { 'Content-Type': 'multipart/form-data' },
        }
      );
      queryClient.refetchQueries(['public-profile']);
    } catch (err) {
      console.log('errr uploading the image', err);
    }
  };

  if (isLoading) {
    return <Loading />;
  }

  console.log('up', data);

  return (
    <Box display='flex' justifyContent='center' style={{ minHeight: '65vh' }}>
      <Paper
        className={classes.profileContainer}
        variant='outlined'
        elevation={1}
      >
        <Box>
          <Avatar
            src={data[0].image}
            style={{ width: '70px', height: '70px', margin: 'auto' }}
          ></Avatar>
          <Typography
            variant='h6'
            align='center'
            style={{ marginTop: '10px' }}
            gutterBottom
          >
            {' '}
            {data[0].first_name && data[0].first_name}{' '}
            {data[0].last_name && data[0].last_name}
          </Typography>
          <form
            onSubmit={handleProfilePictureSubmit}
            style={{
              margin: 'auto',
              marginTop: '1rem',
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <input
              type='file'
              accept='image/*'
              onChange={(e) => setPicture(e.target.files[0])}
            />
            <Button
              variant='outlined'
              style={{ marginTop: '10px' }}
              type='submit'
              size='small'
            >
              Submit
            </Button>
          </form>

          <Typography align='center' gutterBottom>
            {data[0].user_bio && data[0].user_bio}
          </Typography>
          <Divider />
        </Box>
        <Box
          display='flex'
          justifyContent='space-between'
          style={{ margin: '1rem 0' }}
          alignItems='center'
        >
          <Typography variant='h6'>Skin Type: </Typography>
          <Typography>{data[0].skin_type && data[0].skin_type} </Typography>
          <Button
            variant='outlined'
            color='primary'
            onClick={() => setShowEditProfile(true)}
          >
            Edit Info
          </Button>
        </Box>
        <Box style={{ marginBottom: '1rem' }}>
          <Typography variant='h6'>Skincare Routine </Typography>
        </Box>
        <Box>
          <Typography
            style={{ fontWeight: 'bold' }}
            variant='subtitle1'
            gutterBottom
          >
            Morning Routine
          </Typography>
          <Typography>
            {data[0].morning_skincare_routine
              ? data[0].morning_skincare_routine
              : 'No morning routine'}
          </Typography>
        </Box>
        <Box style={{ marginTop: '1rem' }}>
          <Typography
            style={{ fontWeight: 'bold' }}
            variant='subtitle1'
            gutterBottom
          >
            Evening Routine
          </Typography>
          <Typography>
            {data[0].evening_skincare_routine
              ? data[0].evening_skincare_routine
              : 'No evening routine'}
          </Typography>
        </Box>
      </Paper>
      {showEditProfile && (
        <EditPublicProfile setShowEditProfile={setShowEditProfile} />
      )}
    </Box>
  );
};

export default PublicProfile;
