import { Switch, Route } from 'react-router-dom';
import Header from './components/layout/header';
import Navbar from './components/layout/navbar';
import Footer from './components/layout/footer';
import Signup from './screens/signup';
import Home from './screens/home';
import ProductDetails from './screens/productDetails';
import Sigin from './screens/signin';
import Cart from './screens/cart';
import ProductByCategory from './screens/productByCategory';
import ProductByBrand from './screens/ProductByBrand';
import Checkout from './screens/checkout';
import PublicProfile from './screens/publicProfile';
import ScrollToTop from './utils/scrollToTop';
import Profile from './screens/profile';
import VerifyEmail from './screens/verifyEmail';
import { ToastContainer } from 'react-toastify';
import SkinCareRoutines from './screens/skincare-routines';
import ForgotPassword from './screens/forgotPassword';
import ResetPassword from './screens/resetPassword';
import AboutUs from './screens/aboutUs';
import ProductBySubCategory from './screens/productBySubCategory';
import NotFound from './screens/notFound';
import ProductBrand from './screens/productBrand';
import BankDetails from './screens/bankDetails';
import Favourite from './screens/favourite';
import ESewaPaymentFailed from './screens/esewa_payment_failed';
import ESewaPaymentSuccess from './screens/esewa_payment_success';
import AdvertisementProducts from './screens/advertisementProducts';
import Contact from './screens/contact';
import Delivery from './screens/delivery';

const App = () => {
  return (
    <>
      <ToastContainer position='bottom-right' />
      <Header />
      <Navbar />
      <ScrollToTop />
      <Switch>
        <Route path='/' exact component={Home} />
        <Route path='/about' exact component={AboutUs} />
        <Route path='/contact' exact component={Contact} />
        <Route path='/delivery' exact component={Delivery} />
        <Route path='/signup' exact component={Signup} />
        <Route path='/signin' exact component={Sigin} />
        <Route path='/cart' exact component={Cart} />
        <Route path='/checkout' exact component={Checkout} />
        <Route path='/product/:id' exact component={ProductDetails} />
        <Route
          path='/ad_products/:id'
          exact
          component={AdvertisementProducts}
        />
        <Route path='/category/:id' exact component={ProductByCategory} />
        <Route
          path='/sub_category/:id'
          exact
          component={ProductBySubCategory}
        />
        <Route path='/brand/:id' exact component={ProductByBrand} />
        <Route path='/profile' exact component={Profile} />
        <Route path='/public-profile' exact component={PublicProfile} />
        <Route path='/skincare-routines' exact component={SkinCareRoutines} />
        <Route path='/forgot-password' exact component={ForgotPassword} />
        <Route path='/bank-details' exact component={BankDetails} />
        <Route path='/brands' exact component={ProductBrand} />
        <Route path='/my-favourites' exact component={Favourite} />
        <Route
          path='/esewa_payment_failed'
          exact
          component={ESewaPaymentFailed}
        />
        <Route
          path='/esewa_payment_success'
          exact
          component={ESewaPaymentSuccess}
        />
        <Route path='/accounts/signup/verify/' exact component={VerifyEmail} />
        <Route
          path='/accounts/password/reset/verify/'
          exact
          component={ResetPassword}
        />
        <Route component={NotFound} />
      </Switch>
      <Footer />
    </>
  );
};

export default App;
